import { useStudents } from "@/hooks/useStudents";
import { useTeachers } from "@/hooks/useTeachers";

export default function StatsCards() {
  const { students, isLoading, mutate } = useStudents();
  const { teachers, isLoading, mutate } = useTeachers();



  const stats = [
    { name: "Total Élèves", value: students.length },
    { name: "Classes", value: "24" },
    { name: "Professeurs", value: "32" },
  ];

  return (
    <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
      {stats.map((stat) => (
        <div key={stat.name} className="rounded-lg border bg-card p-6 text-card-foreground shadow-sm">
          <div className="text-2xl font-bold">{stat.value}</div>
          <div className="text-sm text-muted-foreground">{stat.name}</div>
        </div>
      ))}
    </div>
  );
}
